	module.exports = {"pathContext":{"frontmatter":{"path":"contributing/opening-a-pull-request","title":"Opening a Pull Request","hasPlayground":false,"initialPlayground":null,"redirect":null},"html":"<h1>Opening a Pull Request</h1>\n<p>Before opening your first pull request to Yoga you have to know how to get the code,\ninstall build time dependencies, and test the code locally.</p>\n<h3>Clone</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$> git clone https://github.com/facebook/yoga.git\n$> cd yoga</code></pre>\n      </div>\n<h3>Install dependencies</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$> git submodule init\n$> git submodule update\n$> brew install buck</code></pre>\n      </div>\n<h3>Build and Test</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$> buck build //:yoga\n$> buck test //:yoga</code></pre>\n      </div>\n<h2>Making a Change</h2>\n<p>Now all you need to do is make your change and test it before submitting a pull request for review.\nBelow is the general structure of the repo and where you may want to make your change. One you have\nmade your change see the <a href=\"/contributing/testing\">testing documentation</a> for more on how to test your change.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>/yoga\n<span class=\"token operator\">|</span>-- yoga                  <span class=\"token comment\"># Home to the main Yoga codebase written in C++. Any algorithmic changes should be made here</span>\n<span class=\"token operator\">|</span>-- lib                   <span class=\"token comment\"># Yoga external dependencies. Be thoughtful adding any new ones</span>\n<span class=\"token operator\">|</span>-- tests                 <span class=\"token comment\"># Yoga's C++ test suite. Both manaul and generated tests</span>\n<span class=\"token operator\">|</span>-- gentest\n<span class=\"token operator\">|</span>   <span class=\"token operator\">|</span>-- fixtures          <span class=\"token comment\"># html fixtures for generated tests    </span>\n<span class=\"token operator\">|</span>-- java         \n<span class=\"token operator\">|</span>   <span class=\"token operator\">|</span>-- com/facebook/yoga <span class=\"token comment\"># Java binding code</span>\n<span class=\"token operator\">|</span>   <span class=\"token operator\">|</span>-- jni               <span class=\"token comment\"># JNI binding code</span>\n<span class=\"token operator\">|</span>-- yogacore              <span class=\"token comment\"># Android bindings without View support</span>\n<span class=\"token operator\">|</span>-- android               <span class=\"token comment\"># Android View bindings</span>\n<span class=\"token operator\">|</span>-- YogaKit               <span class=\"token comment\"># iOS UIView bindings</span>\n<span class=\"token operator\">|</span>-- javascript            <span class=\"token comment\"># emscripten / javascript bindings</span>\n<span class=\"token operator\">|</span>-- csharp                <span class=\"token comment\"># .NET bindings in c#</span>\n</code></pre>\n      </div>"}}

